<template>
    <div ref="scrollContent" class="integral-list">
        <div class="integral-content">
            <integral-cell class="integral-cell" v-for="(dataItem, index) in goodsArr" :cellindex="index" :goods="dataItem" @click.native="cellClickHandle">
            </integral-cell>
        </div>
        <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { formatDate } from "../../../common/js/date.js";
import EmptyBg from "../../popupBox/emptyBg.vue";
import integralCell from "../../public/integralCell.vue";
export default {
  name: "integral-list",
  data() {
    return {
      goodsArr: [],
      showEmpty: false,
      emptyMsg: "您还未购买过积分商品!",
      memoryScroll:0,
      isLoadDb:false
    };
  },
  components: {
    EmptyBg,
    integralCell
  },
  activated() {
    if(this.$refs.scrollContent){
      this.$refs.scrollContent.scrollTop=this.memoryScroll;
    }
    if(this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  beforeRouteLeave(to, from, next) {
    this.memoryScroll=this.$refs.scrollContent.scrollTop;
    next();
  },
  created() {
    this.showEmpty = false;
    this.getGoodsData();
  },
  methods: {
    cellClickHandle(event) {
      let curTarget = event.currentTarget;
      let cellIndex = curTarget.getAttribute("cellindex");
      let dataItem=this.goodsArr[cellIndex];
      if(dataItem.hadUesed) return;
      this.$router.push({ name:'integralQRcode', params:{id:dataItem.id,name:dataItem.names,imgUrl:dataItem.url,num:dataItem.num}});
    },
    getGoodsData() {
      this.$http
        .post(this.path + `php/Route.php`, {
          type:'sourcebuyhistroy',
          uid:this.userid,
          page:0
        })
        .then(response => {
          let data = response.body.arr;
          let budArr = [];
          data.forEach((val, index, arr) => {
            let tempDate = new Date(val.times * 1000);
            let buyTime = formatDate(tempDate, "yyyy-MM-dd");
            let usetime="";
            let ticketUsed;
            if(val.state.toString()=="2"){
              ticketUsed=true;
              tempDate = new Date(val.usetime * 1000);
              usetime=formatDate(tempDate, "yyyy-MM-dd");
            }else{
              ticketUsed=false;
            }
            budArr.push({
              id: val.oid.toString(),
              names: val.gname,
              url: this.path + "img/goods2.jpg",
              buytm: buyTime,
              price: val.source,
              dec:val.message,
              hadUesed:ticketUsed,
              num:val.nums,
              usetm:usetime
            });
          });
          this.goodsArr = budArr;
          if (this.goodsArr.length == 0) this.showEmpty = true;
          this.$store.dispatch("toggleLoading", false);
          this.isLoadDb=true;
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    }
  },
  computed: {
    ...mapState(["path", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.integral-list {
    height: 100%;

    .integral-content {
        width: 100%;
        padding-bottom: 1px;

        .integral-cell{
            width:100%;
            background-color: #fff;
            margin-bottom 0.07rem;
        }
    }
}
</style>



